<template>
  <v-dialog
    :value="true"
    persistent
    width="500"
  >
    <v-card>
      <v-toolbar
        flat
        dense
      >
        <v-toolbar-title>
          Input Data Aturan
        </v-toolbar-title>
      </v-toolbar>
      <v-card-text>
        <v-select
          :items="options"
          v-model="item.produksi"
          label="Produksi"
        />
        <v-select
          :items="options"
          v-model="item.penjualan"
          label="Penjualan"
        />
        <v-select
          :items="options"
          v-model="item.persediaan"
          label="Persediaan"
        />
        <v-select
          :items="options"
          v-model="item.permintaan"
          label="Permintaan"
        />
      </v-card-text>
      <v-card-actions>
        <v-btn
          flat
          dark
          color="green"
          @click="addAturan"
        >
          Simpan
        </v-btn>
        <v-btn
          flat
          dark
          color="pink"
          @click="$router.back()"
        >
          Batal
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import Vue from "vue";
import evbus from "@/evbus";
import options from "@/options";
import defaults from "@/defaults";
import RuleAPI from "@/services/rule";

export default Vue.extend({
  name: "AddAturan",
  data: () => ({
    options: [ ...options.rule ],
    item: { ...defaults.rule }
  }),
  methods: {
    async addAturan() {
      evbus.$emit("loading-on");
      try {
        const payload = { ...this.item };
        await RuleAPI.create(payload);
      } catch (err) {
        console.log(err);
      } finally {
        evbus.$emit("loading-off");
        this.$router.back();
      }
    }
  },
  mounted() {

  }
});
</script>

